.container {
  width: 100%;
  min-width: 320px;
  padding-left: 15px;
  padding-right: 15px;

  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding-left: 227px;
    padding-right: 227px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 65px;
    padding-right: 65px;
  }
}
